body {
  background-color: rgba(255, 255, 255, 0);
}

.container {
  font-family: "Raleway", sans-serif;
  box-shadow: 0 15px 30px 1px rgba(128, 128, 128, 0.31);
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  margin: 4em auto;
  height: 610px;
  width: 1050px;
}

.imgcontainer {
  position: relative;
  overflow: hidden;
  float: left;
  width: 50%;
  z-index: 2;
}

.imgOne {
  width: 210%;
  transform: translateX(-26%);
}

.login-container {
  padding-top: 10px;
  padding-left: 570px;
  height: 610px;
  background-color: rgb(224, 229, 235);
  font-size: xx-large;
  font-weight: 100;
  line-height: 0.3;
  text-align: left;
}

.login-container h6 {
  color: rgb(124, 120, 120);
}

/* invalid */

.invalid-id {
  position: relative;
  bottom: 10px;
  font-size: 16px;
  color: red;
  font-weight: bold;
}

.login-form {
  position: relative;
  bottom: 25px;
}

/* form input */

input[class="login-form-input"] {
  width: 340px;
  height: 8px;

  margin-top: 2%;
  padding: 15px;

  font-size: 18px;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  color: #000000;

  outline: none;
  border: none;

  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

.form-select {
  width: 370px;
  height: 37px;
  padding: 0 10px;
  margin-top: 5%;
  margin-bottom: 3%;

  font-size: 16px;
  outline: none;
  border: none;

  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

.form-option {
  margin-top: 5px;
  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

.form-checkbox {
  margin-top: 15px;
}

.form-checkbox-label {
  margin-top: 5px;
  font-size: 16px;
  font-weight: bold;
}

.btn-login {
  display: inline-block;
  color: #ffffff;

  width: 370px;
  height: 37px;

  padding: 0 20px;
  background: #0275d8;
  border-radius: 50px;
  transition: 0.2s linear;

  outline: none;
  border: none;

  cursor: pointer;
  text-align: center;

  margin: 7% auto;
  letter-spacing: 0.05em;
}

a {
  color: black;
  margin-left: 20px;
  font-size: 16px;
  float: left;
  display: flex;
  width: 40%;
  text-decoration: none;
}

a:hover {
  font-weight: bold;

  text-decoration: underline;
}

a:first-child {
  font-style: italic;
}

a:last-child {
  font-weight: bold;
}

.link-register {
  font-weight: bold;

  color: black;
  margin-left: 20px;
  font-size: 16px;
  float: left;
  display: flex;
  width: 40%;
  text-decoration: none;
}

.link-register:hover {
  text-decoration: underline;
}

#eye {
  color: #333;
  height: 5px;
  margin-left: -20px;
  padding: 15px 9px 19px 0px;
  font-size: 16px;
  float: right;
  position: relative;
  right: 25%;
  top: 5px;
  z-index: 5;

  cursor: pointer;
}

#eye-red {
  color: red;
  height: 5px;
  margin-left: -20px;
  padding: 15px 9px 19px 0px;
  font-size: 16px;
  float: right;
  position: relative;
  right: 25%;
  top: 5px;
  z-index: 5;

  cursor: pointer;
}

