@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap");

.container {
  font-family: "Raleway", sans-serif;
  box-shadow: 0 15px 30px 1px rgba(128, 128, 128, 0.31);
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  margin: 4em auto;
  height: 610px;
  width: 1050px;
}

.imgcontainer {
  position: relative;
  overflow: hidden;
  float: left;
  width: 50%;
  z-index: 2;
}

.imgOne {
  width: 210%;
  transform: translateX(-26%);
}

.register-container {
  padding-top: 5px;
  padding-left: 570px;
  height: 610px;
  background-color: rgb(224, 229, 235);
  font-size: x-large;
  font-weight: 100;
  line-height: 0.3;
  text-align: left;
}

.login-container h6 {
  color: rgb(124, 120, 120);
}

.register-invalid-id {
  position: relative;
  bottom: 15px;
  font-size: 16px;
  color: red;
  font-weight: bold;
}

.register-valid-id {
  position: relative;
  bottom: 15px;
  font-size: 16px;
  color: rgb(43, 182, 43);
  font-weight: bold;
}

/* form input */

.register-form {
  position: relative;
  bottom: 30px;
}

input[class="register-form-input"] {
  width: 300px;
  height: 4px;

  margin-top: 2%;
  padding: 15px;

  font-size: 18px;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  color: #000000;

  outline: none;
  border: none;

  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

input[class="register-form-input-invalid"] {
  width: 300px;
  height: 4px;

  margin-top: 2%;
  padding: 15px;

  font-size: 18px;
  font-weight: bold;
  font-family: "Raleway", sans-serif;
  color: #000000;

  outline: none;
  border: none;

  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

.register-form-select {
  width: 330px;
  height: 37px;
  padding: 0 10px;
  margin-top: 2%;
  margin-bottom: 0%;

  font-size: 16px;
  outline: none;
  border: none;

  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

.form-option {
  margin-top: 5px;
  border-radius: 50px 50px 50px 50px;
  transition: 0.2s linear;
}

.form-checkbox {
  margin-top: 15px;
}

.form-checkbox-label {
  margin-top: 5px;
  font-size: 16px;
  font-weight: bold;
}

.btn-register {
  display: inline-block;
  color: #ffffff;

  width: 330px;
  height: 37px;

  padding: 0 20px;
  background: #0275d8;
  border-radius: 50px;
  transition: 0.2s linear;

  outline: none;
  border: none;

  cursor: pointer;
  text-align: center;

  margin: 4% auto;
  letter-spacing: 0.05em;
}

a {
  color: black;
  margin-left: 20px;
  font-size: 16px;
  float: left;
  display: flex;
  width: 40%;
  text-decoration: none;
}

a:hover {
  font-weight: bold;

  text-decoration: underline;
}

a:first-child {
  font-style: italic;
}

a:last-child {
  font-weight: bold;
}

.link-register {
  font-weight: bold;

  color: black;
  margin-left: 20px;
  font-size: 18px;
  float: left;
  display: flex;
  width: 50%;
  text-decoration: none;
}

.link-register:hover {
  text-decoration: underline;
}

/*----text area----*/

.register-form-input {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  outline: none;
}

.register-form-input:focus {
  box-shadow: 0 0 5px rgba(81, 203, 238, 1);
}

.register-form-input-invalid {
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  outline: none;
  box-shadow: 0 0 5px rgb(238, 67, 67);
}

.resgiter-success {
  margin-top: 15%;
  margin-right: 10%;
  text-align: center;
}

.imageTwo {
  width: 40%;
  align-items: center;
}

.resgiter-success-login {
  margin: 100px 200px;
}
