@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap");

.container {
  font-family: "Raleway", sans-serif;
  box-shadow: 0 15px 30px 1px rgba(128, 128, 128, 0.31);
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  border-radius: 5px;
  overflow: hidden;
  margin: 4em auto;
  height: 610px;
  width: 1050px;
}

.nav-container {
  padding: 0px 0px 20px 40px;

  position: relative;
  text-align: left;

  background-color: rgb(224, 229, 235);
  height: 55px;
  list-style-type: none;
  /* float: left; */
  width: 100%;
  display: inline-flex;
}

.btn-logout {
  position: absolute;
  top: 20px;
  right: 100px;
  background-color: white;
  color: black;
  border: 2px solid black;
  width: 150px;
  height: 40px;
  border-radius: 20px;
  font-size: 18px;
  cursor: pointer;
}

.btn-logout:hover {
  background-color: rgb(206, 94, 94);
  color: white;
}

.users-container {
  position: relative;
  border-left: 2px solid rgb(224, 229, 235);
  text-align: left;

  height: 480px;
  width: 78%;
}

.panchayath-container {
  position: relative;
  margin: 30px;
  padding: 10px 30px 30px;
  border: 2px solid rgb(220, 223, 220);
  height: 200px;
  width: 350px;
}

.panchayath-container h4 {
  font-weight: bold;
}

.btn-edit:hover {
  border-color: white;
  background-color: rgb(94, 206, 137);
  color: white;
}

.btn-edit {
  height: 30px;
  width: 80px;
  background-color: white;
  color: black;
  border: 2px solid black;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  position: absolute;
  right: 42%;
  /* -ms-transform: translateY(-50%);
  transform: translateY(-50%); */
}

.btn-update {
  height: 30px;
  width: 80px;
  background-color: white;
  color: black;
  border: 2px solid black;
  border-radius: 20px;
  font-size: 16px;
  cursor: pointer;
  position: absolute;
  right: 42%;
  bottom: 7%;
  /* -ms-transform: translateY(-50%);
  transform: translateY(-50%); */
}

.btn-update:hover {
  border-color: white;
  background-color: rgb(206, 94, 94);
  color: white;
}

.sideBar {
  float: left;
}

/* Three image containers (use 25% for four, and 50% for two, etc) */
.column {
  float: left;
  width: 20%;
  padding: 5px;
}

/* Clear floats after image containers */
.row::after {
  content: "";
  clear: both;
  display: table;
}

.sidebar {
  padding-top: 30px;
  text-align: center;
}

.sidebar img {
  vertical-align: middle;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.sidebar .details {
  text-align: left !important;
  line-height: 20%;
}

.link-register {
  font-weight: bold;

  color: black;
  margin-left: 20px;
  font-size: 16px;
  float: left;
  display: flex;
  width: 40%;
  text-decoration: none;
}

.link-register:hover {
  text-decoration: underline;
}

input[class="edit-form-input"] {
  margin: 3% 0;
}

#edit-label {
  color: black;
}
